<template>
  <home-layout></home-layout>
</template>

<script>

import tool from '@/common/es/tool'
import conn from '@/service/conn'
import conn_util from '@/common/es/conn_util'

import HomeLayout from './HomeLayout.vue'

export default {
    name: 'Home',
    components: {
      HomeLayout
    },
    created() {
      // console.log('页面发生展示时调用。')
    },
    mounted() {

      this.loadFreights()
    },
    methods: {

      loadFreights() {
        console.log('拉取蔬菜')

        return new Promise((res, rej) => {
          conn.freight.getList_Freight({}, true).then(e => {
            
            if (e.length > 0) {
              const res = []
              e.map(e => {
                const { id, num, unit, price, named } = e
                res.push({
                  id, num, unit, price, named
                })
              })

              tool.storage._set('invoice_freights', JSON.stringify(res))
            } else {
              tool.storage._set('invoice_freights', JSON.stringify([]))
            }
          }).catch(e => {
            
          })
        })

      }
    }
}
</script>

<style lang="sass">

</style>


