<template>
  <div>
    <menu-item-wrapper v-for="(m, i) in menu" :key="m.header.name" :paramId="i">
      
      <!-- HEADER -->
      <div class="d-inline fa-pos-fix float-left" slot="headerLeading">
        <i :class="m.header.icon" class="fa m-0"></i>
      </div>

      <div slot="headerText" class="d-inline float-left fa-text"> {{ m.header.name }} </div>

      <div class="d-inline fa-pos-fix float-right" slot="headerAction">
        <i :class="m.header.action" class="fa h5 m-0"></i>
      </div>

      <!-- CONTENT -->
      <div slot="slot_content" v-for="c in m.children" :key="c.name" @click="lunch(c.link)" tag="div">
        {{ c.name }}
      </div>
      
    </menu-item-wrapper>

  </div>
</template>

<script>
import MenuItemWrapper from './LeftMenu/MenuItemWrapper.vue'

export default {
  name: 'LeftMenu',
  components: {
    MenuItemWrapper,
  },
  data() {
    return {
      listing_plus_uri: '/home/listing_plus',
      prices_plus_uri: '/home/prices_plus',
      menu: [
        {
          header: {
            name: '發票',
            icon: 'fa-table',
            action: 'fa-angle-right'
          },
          children: [ 
            {
              name: '全部',
              link: '/home/listing'
            },
            {
              name: '新增',
              link: '/home/listing_plus'
            },
            {
              name: '記錄',
              link: '/home/record_dashboard'
            }
            /*
            {
              name: '修改',
              link: '/home/listing_update'
            }
            */
          ]
        },
        {
          header: {
            name: '報價單',
            icon: 'fa-sticky-note-o',
            action: 'fa-angle-right'
          },
          children: [
            {
              name: '全部',
              link: '/home/prices'
            },
            {
              name: '新增',
              link: '/home/prices_plus'
            },
            /*
            {
              name: '修改',
              link: '/home/prices_update'
            },
            */
          ]
        },
        {
          header: {
            name: '客戶',
            icon: 'fa-user-o',
            action: 'fa-angle-right'
          },
          children: [ 
            {
              name: '全部',
              link: '/home/member'
            },
            {
              name: '新增',
              link: '/home/member_plus'
            },
            {
              name: '個人中心',
              link: '/home/member_msg_center'
            },
            /*
            {
              name: '修改',
              link: '/home/member_update'
            },
            */
          ]
        },
        {
          header: {
            name: '產品',
            icon: 'fa-cubes',
            action: 'fa-angle-right'
          },
          children: [
            {
              name: '全部',
              link: '/home/freight'
            },
          ]
        },
        {
          header: {
            name: '標籤',
            icon: 'fa-tags',
            action: 'fa-angle-right'
          },
          children: [
            {
              name: '全部',
              link: '/home/tag'
            },
          ]
        },
        {
          header: {
            name: '其它',
            icon: 'fa-cog',
            action: 'fa-angle-right'
          },
          children: [
            {
              name: '主面板',
              link: '/home/dashboard_new'
            },
          ]
        },
      ]
    }
  },
  methods: {
    _go(uri) {
      this.$router.push({
        path: uri
      })
    },
    _confirm(uri) {
      const res = confirm('確定要離開此界面嗎？')

      if (res) {
        this._go(uri)
      }
    },
    lunch(uri) {
      const from = this.$route.path 

      if (uri != from) {

        if ((from == this.listing_plus_uri)) {
          this._confirm(uri)
        } 
        else if ((from == this.prices_plus_uri)) {
          this._confirm(uri)
        } 
        else {
          this._go(uri)
        }
      }
    }
  }
}
</script>

<style lang="sass">
@import "@/common/sass/base.sass"
</style>


